<template>
<div>
  <span>{{display}}</span>
  <div>
    <operation-button @click="operate('÷')" bewerking="delen"></operation-button>
    <operation-button @click="operate('x')" bewerking="vermenigvuldigen"></operation-button>
    <operation-button @click="operate('-')" bewerking="aftrekken"></operation-button>
    <operation-button @click="operate('+')" bewerking="optellen"></operation-button>
    <button @click="calculate">=</button>
  </div>
  <calc-button :getal="9" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="8" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="7" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="6" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="5" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="4" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="3" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="2" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="1" @update-totaal="addNumber($event)"></calc-button>
  <calc-button :getal="0" @update-totaal="addNumber($event)"></calc-button>
</div>
</template>

<script>
import OperationButton from './components/OperationButton.vue'
import CalcButton from './components/CalcButton.vue'

export default {
  name: 'App',
  components: {
    OperationButton,
    CalcButton
  },
  data(){
    return {
      operator: "",
      operand1: 0,
      currentInput: 0,
      state: {
        operating: false,
        calculated: false,
      }
    }
  },
  computed: {
    display(){
      return Number(this.currentInput)
    }
  },
  methods: {
    operate(symbol){
      this.state.operating = true

      this.operator = symbol
      this.operand1 = this.currentInput
    },
    addNumber(number){
      if(this.state.operating || this.state.calculated){
        this.currentInput = number
        this.state.operating = false
        this.state.calculated = false
      } else {
        this.currentInput = Number("" + this.currentInput + number)
      }
    },
    calculate(){
      switch(this.operator){
        case '÷':
          this.currentInput = this.operand1 / this.currentInput
          break
        case 'x':
          this.currentInput = this.operand1 * this.currentInput
          break
        case '-':
          this.currentInput = this.operand1 - this.currentInput
          break
        case '+':
          this.currentInput = this.operand1 + this.currentInput
          break
      }
      this.state.calculated = true
    }
  }
}
</script>

<style>
#app > div {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: repeat(5, 40px);
  justify-items: stretch;
}
div > span {
  grid-column: span 3;
  place-self: center;
}
div > span:last-of-type{
  grid-row: 1;
}
#app > div > button:last-of-type{
  grid-column: span 3;
  place-self: stretch;
}
span + div {
  grid-row: span 5;
  display: grid;
  grid-template-rows: repeat(5, 40px);
}
</style>
