<template>
  <button>{{bewerkingsSymbool}}</button>
</template>

<script>
export default {
  name: 'OperationButton',
  props: {
    bewerking: String
  },
  computed: {
    bewerkingsSymbool() { 
    return  this.bewerking === "delen" ? "÷" : 
            this.bewerking === "vermenigvuldigen" ? "x" : 
            this.bewerking === "aftrekken" ? "-" :
            this.bewerking === "optellen" ? "+" : 
            null
    }
  },
}
</script>

